/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable @typescript-eslint/no-unused-vars */
'use client';

import React, { useState, useEffect } from 'react';
import {
    BarChart,
    Bar,
    XAxis,
    YAxis,
    CartesianGrid,
    Tooltip,
    ResponsiveContainer,
    PieChart,
    Pie,
    Cell,
    LineChart,
    Line,
    AreaChart,
    Area
} from 'recharts';
import {
    DollarSign,
    Users,
    Calendar,
    MapPin,
    TrendingUp,
    AlertTriangle,
    CheckCircle,
    RefreshCw,
    LogIn
} from 'lucide-react';

// Import your existing API service
// import api from '@/lib/api'; // Adjust path as needed

// TypeScript interfaces for analytics data (keep your existing interfaces)
interface EventRevenueData {
    eventId: number;
    eventName: string;
    totalRevenue: number;
    attendeeCount: number;
    ticketsSold: number;
}

interface RevenueAnalytics {
    totalRevenue: number;
    totalAttendees: number;
    activeEvents: number;
    totalVenues: number;
    events: EventRevenueData[];
}

interface PaymentMethodData {
    paymentMethod: string;
    orderCount: number;
    percentage: number;
    totalAmount: number;
}

interface PaymentAnalytics {
    methods: PaymentMethodData[];
}

interface CapacityData {
    eventId: number;
    eventName: string;
    maxCapacity: number;
    ticketsSold: number;
    utilizationPercentage: number;
}

interface CapacityAnalytics {
    events: CapacityData[];
}

interface AgeGroupData {
    ageGroup: string;
    count: number;
    percentage: number;
}

interface GenderData {
    gender: string;
    count: number;
    percentage: number;
}

interface DemographicsAnalytics {
    ageDistribution: AgeGroupData[];
    genderDistribution: GenderData[];
}

interface CheckInHourlyData {
    hour: string;
    checkInCount: number;
    cumulativeCount: number;
}

interface CheckInAnalytics {
    hourlyPattern: CheckInHourlyData[];
    totalCheckIns: number;
    totalTicketsSold: number;
    attendanceRate: number;
}

interface VenuePerformanceData {
    venueId: number;
    venueName: string;
    eventCount: number;
    avgAttendance: number;
    totalRevenue: number;
    avgRating: number;
}

interface VenueAnalytics {
    performance: VenuePerformanceData[];
}

interface SeasonalTrendData {
    month: string;
    eventCount: number;
    totalRevenue: number;
    totalAttendance: number;
}

interface SeasonalAnalytics {
    monthlyTrends: SeasonalTrendData[];
}

interface TicketTypeData {
    typeName: string;
    price: number;
    sold: number;
}

interface LowAttendanceEventData {
    eventId: number;
    eventName: string;
    maxCapacity: number;
    ticketsSold: number;
    utilizationPercentage: number;
    daysUntilEvent: number;
    ticketTypes: TicketTypeData[];
    potentialIssues: string[];
    recommendations: string[];
}

interface LowAttendanceAnalytics {
    events: LowAttendanceEventData[];
}

interface AnalyticsData {
    revenue: RevenueAnalytics;
    payments: PaymentAnalytics;
    capacity: CapacityAnalytics;
    demographics: DemographicsAnalytics;
    checkIns: CheckInAnalytics;
    venues: VenueAnalytics;
    seasonal: SeasonalAnalytics;
    lowAttendance: LowAttendanceAnalytics;
}

interface StatCardProps {
    title: string;
    value: string;
    icon: React.ComponentType<any>;
    trend?: string;
    color?: string;
}

const OrganizerAnalytics: React.FC = () => {
    const [selectedPeriod, setSelectedPeriod] = useState<string>('last30days');
    const [loading, setLoading] = useState<boolean>(false);
    const [analyticsData, setAnalyticsData] = useState<AnalyticsData | null>(null);
    const [isAuthenticated, setIsAuthenticated] = useState<boolean>(false);
    const [authError, setAuthError] = useState<string | null>(null);

    // Use consistent token key with your api.ts
    const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:5251';

    // Check authentication status
    const checkAuthStatus = (): boolean => {
        const token = localStorage.getItem('authToken'); // Match your api.ts
        const user = localStorage.getItem('user');

        if (!token) {
            setIsAuthenticated(false);
            setAuthError('No authentication token found');
            return false;
        }

        // Optional: Check if token is expired
        try {
            const tokenPayload = JSON.parse(atob(token.split('.')[1]));
            const isExpired = tokenPayload.exp * 1000 < Date.now();

            if (isExpired) {
                localStorage.removeItem('authToken');
                localStorage.removeItem('user');
                setIsAuthenticated(false);
                setAuthError('Session expired');
                return false;
            }
        } catch (error) {
            console.error('Error parsing token:', error);
            setIsAuthenticated(false);
            setAuthError('Invalid token format');
            return false;
        }

        setIsAuthenticated(true);
        setAuthError(null);
        return true;
    };

    // Analytics API service using your existing pattern
    const analyticsApi = {
        getRevenueAnalytics: async (period: string) => {
            const token = localStorage.getItem('authToken');
            const response = await fetch(`${API_BASE_URL}/api/analytics/revenue-by-event?period=${period}`, {
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json'
                }
            });
            if (!response.ok) throw new Error(`Revenue API failed: ${response.status}`);
            return response.json();
        },

        getPaymentAnalytics: async (period: string) => {
            const token = localStorage.getItem('authToken');
            const response = await fetch(`${API_BASE_URL}/api/analytics/payment-methods?period=${period}`, {
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json'
                }
            });
            if (!response.ok) throw new Error(`Payment API failed: ${response.status}`);
            return response.json();
        },

        getCapacityAnalytics: async (period: string) => {
            const token = localStorage.getItem('authToken');
            const response = await fetch(`${API_BASE_URL}/api/analytics/event-capacity?period=${period}`, {
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json'
                }
            });
            if (!response.ok) throw new Error(`Capacity API failed: ${response.status}`);
            return response.json();
        },

        getDemographicsAnalytics: async (period: string) => {
            const token = localStorage.getItem('authToken');
            const response = await fetch(`${API_BASE_URL}/api/analytics/attendee-demographics?period=${period}`, {
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json'
                }
            });
            if (!response.ok) throw new Error(`Demographics API failed: ${response.status}`);
            return response.json();
        },

        getVenueAnalytics: async (period: string) => {
            const token = localStorage.getItem('authToken');
            const response = await fetch(`${API_BASE_URL}/api/analytics/venue-performance?period=${period}`, {
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json'
                }
            });
            if (!response.ok) throw new Error(`Venue API failed: ${response.status}`);
            return response.json();
        },

        getSeasonalAnalytics: async () => {
            const token = localStorage.getItem('authToken');
            const response = await fetch(`${API_BASE_URL}/api/analytics/seasonal-trends`, {
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json'
                }
            });
            if (!response.ok) throw new Error(`Seasonal API failed: ${response.status}`);
            return response.json();
        },

        getLowAttendanceAnalytics: async () => {
            const token = localStorage.getItem('authToken');
            const response = await fetch(`${API_BASE_URL}/api/analytics/low-attendance-events`, {
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json'
                }
            });
            if (!response.ok) throw new Error(`Low attendance API failed: ${response.status}`);
            return response.json();
        }
    };

    const fetchAnalyticsData = async (): Promise<void> => {
        if (!checkAuthStatus()) {
            return;
        }

        setLoading(true);
        setAuthError(null);

        try {
            console.log('ðŸ”„ Fetching analytics data...');

            const [
                revenueData,
                paymentData,
                capacityData,
                demographicsData,
                venueData,
                seasonalData,
                lowAttendanceData
            ] = await Promise.all([
                analyticsApi.getRevenueAnalytics(selectedPeriod),
                analyticsApi.getPaymentAnalytics(selectedPeriod),
                analyticsApi.getCapacityAnalytics(selectedPeriod),
                analyticsApi.getDemographicsAnalytics(selectedPeriod),
                analyticsApi.getVenueAnalytics(selectedPeriod),
                analyticsApi.getSeasonalAnalytics(),
                analyticsApi.getLowAttendanceAnalytics()
            ]);

            // Mock check-in data until backend LINQ issue is fixed
            const checkInData = {
                hourlyPattern: [],
                totalCheckIns: 0,
                totalTicketsSold: revenueData.totalAttendees || 0,
                attendanceRate: 0
            };

            setAnalyticsData({
                revenue: revenueData,
                payments: paymentData,
                capacity: capacityData,
                demographics: demographicsData,
                checkIns: checkInData,
                venues: venueData,
                seasonal: seasonalData,
                lowAttendance: lowAttendanceData
            });

            console.log('âœ… Analytics data loaded successfully');

        } catch (error: any) {
            console.error('âŒ Error fetching analytics:', error);

            if (error.message.includes('401') || error.message.includes('Unauthorized')) {
                localStorage.removeItem('authToken');
                localStorage.removeItem('user');
                setIsAuthenticated(false);
                setAuthError('Authentication expired. Please log in again.');
            } else {
                setAuthError(`Failed to load analytics: ${error.message}`);
            }

            // Use mock data as fallback
            setAnalyticsData(getMockData());
        } finally {
            setLoading(false);
        }
    };

    // Mock data function (keep your existing implementation)
    const getMockData = (): AnalyticsData => ({
        revenue: {
            totalRevenue: 0,
            totalAttendees: 0,
            activeEvents: 0,
            totalVenues: 0,
            events: []
        },
        payments: {
            methods: [
                { paymentMethod: 'Credit Card', orderCount: 0, percentage: 65, totalAmount: 0 },
                { paymentMethod: 'PayPal', orderCount: 0, percentage: 20, totalAmount: 0 },
                { paymentMethod: 'Bank Transfer', orderCount: 0, percentage: 10, totalAmount: 0 },
                { paymentMethod: 'Other', orderCount: 0, percentage: 5, totalAmount: 0 }
            ]
        },
        capacity: { events: [] },
        demographics: {
            ageDistribution: [],
            genderDistribution: []
        },
        checkIns: {
            hourlyPattern: [],
            totalCheckIns: 0,
            totalTicketsSold: 0,
            attendanceRate: 0
        },
        venues: { performance: [] },
        seasonal: { monthlyTrends: [] },
        lowAttendance: { events: [] }
    });

    useEffect(() => {
        if (checkAuthStatus()) {
            fetchAnalyticsData();
        }
    }, [selectedPeriod]);

    const COLORS = ['#3B82F6', '#EF4444', '#10B981', '#F59E0B', '#8B5CF6', '#06B6D4'];

    const StatCard: React.FC<StatCardProps> = ({ title, value, icon: Icon, trend, color = "blue" }) => (
        <div className="bg-white rounded-lg shadow-md p-6 border-l-4 border-blue-500">
            <div className="flex items-center justify-between">
                <div>
                    <p className="text-sm font-medium text-gray-600">{title}</p>
                    <p className="text-2xl font-bold text-gray-900">{value}</p>
                    {trend && (
                        <p className="text-sm text-green-600 flex items-center mt-1">
                            <TrendingUp className="w-4 h-4 mr-1" />
                            {trend}
                        </p>
                    )}
                </div>
                <Icon className="w-8 h-8 text-blue-500" />
            </div>
        </div>
    );

    // Login prompt component
    const LoginPrompt: React.FC = () => (
        <div className="max-w-7xl mx-auto p-6 bg-gray-50 min-h-screen">
            <div className="flex items-center justify-center min-h-screen">
                <div className="bg-white rounded-lg shadow-md p-8 max-w-md w-full text-center">
                    <LogIn className="w-16 h-16 mx-auto mb-4 text-blue-500" />
                    <h2 className="text-2xl font-bold text-gray-900 mb-4">Authentication Required</h2>
                    <p className="text-gray-600 mb-6">
                        Please log in to access analytics data.
                    </p>
                    {authError && (
                        <div className="bg-red-50 border border-red-200 rounded-md p-3 mb-4">
                            <p className="text-red-600 text-sm">{authError}</p>
                        </div>
                    )}
                    <button
                        onClick={() => window.location.href = '/login'} // Adjust to your login route
                        className="w-full bg-blue-500 text-white py-2 px-4 rounded-lg hover:bg-blue-600 transition-colors"
                    >
                        Go to Login
                    </button>
                    <button
                        onClick={checkAuthStatus}
                        className="w-full mt-2 bg-gray-100 text-gray-700 py-2 px-4 rounded-lg hover:bg-gray-200 transition-colors"
                    >
                        Check Authentication
                    </button>
                </div>
            </div>
        </div>
    );

    // Show login prompt if not authenticated
    if (!isAuthenticated) {
        return <LoginPrompt />;
    }

    // Show loading state
    if (!analyticsData) {
        return (
            <div className="flex items-center justify-center min-h-screen">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-500"></div>
            </div>
        );
    }

    return (
        <div className="max-w-7xl mx-auto p-6 bg-gray-50 min-h-screen">
            <div className="mb-8">
                <div className="flex items-center justify-between">
                    <div>
                        <h1 className="text-3xl font-bold text-gray-900 mb-2">Analytics Dashboard</h1>
                        <p className="text-gray-600">Comprehensive insights for your events</p>
                    </div>
                    <div className="flex space-x-2">
                        <button
                            onClick={fetchAnalyticsData}
                            disabled={loading}
                            className="flex items-center px-4 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 disabled:opacity-50"
                        >
                            <RefreshCw className={`w-4 h-4 mr-2 ${loading ? 'animate-spin' : ''}`} />
                            Refresh
                        </button>
                        <button
                            onClick={() => {
                                localStorage.removeItem('authToken');
                                localStorage.removeItem('user');
                                setIsAuthenticated(false);
                            }}
                            className="flex items-center px-4 py-2 bg-gray-500 text-white rounded-lg hover:bg-gray-600"
                        >
                            Logout
                        </button>
                    </div>
                </div>

                {authError && (
                    <div className="mt-4 bg-yellow-50 border border-yellow-200 rounded-md p-3">
                        <p className="text-yellow-700 text-sm">{authError}</p>
                    </div>
                )}

                <div className="mt-4">
                    <select
                        value={selectedPeriod}
                        onChange={(e) => setSelectedPeriod(e.target.value)}
                        className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    >
                        <option value="last7days">Last 7 Days</option>
                        <option value="last30days">Last 30 Days</option>
                        <option value="last3months">Last 3 Months</option>
                        <option value="last6months">Last 6 Months</option>
                        <option value="lastyear">Last Year</option>
                    </select>
                </div>
            </div>

            {/* Rest of your existing analytics components */}
            {/* Key Metrics Cards */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
                <StatCard
                    title="Total Revenue"
                    value={`$${analyticsData.revenue.totalRevenue.toLocaleString()}`}
                    icon={DollarSign}
                    trend={analyticsData.revenue.totalRevenue > 0 ? "+12.5% from last month" : "No data yet"}
                />
                <StatCard
                    title="Total Attendees"
                    value={analyticsData.revenue.totalAttendees.toLocaleString()}
                    icon={Users}
                    trend={analyticsData.revenue.totalAttendees > 0 ? "+8.3% from last month" : "No data yet"}
                />
                <StatCard
                    title="Active Events"
                    value={analyticsData.revenue.activeEvents.toString()}
                    icon={Calendar}
                    trend={analyticsData.revenue.activeEvents > 0 ? "Events running" : "No active events"}
                />
                <StatCard
                    title="Venues Used"
                    value={analyticsData.revenue.totalVenues.toString()}
                    icon={MapPin}
                    trend={analyticsData.revenue.totalVenues > 0 ? "Venue partnerships" : "No venues yet"}
                />
            </div>

            {/* Continue with the rest of your existing analytics components... */}
            {/* Add all your existing charts and visualizations here */}

        </div>
    );
};

export default OrganizerAnalytics;